export default {
  props: ['query'],
  data () {
    return {
      keyword: this.query
    }
  },
  methods: {
    confirmHandler () {
      // 为空不跳转
      if (!this.keyword) {
        return
      }
      this.$emit('confirm', this.keyword)
    }
  },
  watch: {
    query (newValue) {
      this.keyword = newValue
    }
  }
}
